"use client";

import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table";
import { Badge } from "@/components/ui/badge";
import { Button } from "@/components/ui/button";
import { Eye, FileText } from "lucide-react";
import { Custodia } from "@/lib/types";
import { format } from "date-fns";
import { es } from "date-fns/locale";

interface CustodyTableProps {
  custodias: Custodia[];
}

export function CustodyTable({ custodias }: CustodyTableProps) {
  const getEstadoColor = (estado: Custodia["estado"]) => {
    switch (estado) {
      case "Registrado":
        return "bg-blue-100 text-blue-800";
      case "Entregado":
        return "bg-green-100 text-green-800";
      case "Anulado":
        return "bg-red-100 text-red-800";
      case "En Almacén":
        return "bg-yellow-100 text-yellow-800";
      default:
        return "bg-gray-100 text-gray-800";
    }
  };

  return (
    <div className="bg-white rounded-lg shadow-sm border overflow-hidden">
      <Table>
        <TableHeader>
          <TableRow>
            <TableHead>N° Certificado</TableHead>
            <TableHead>Tipo de Bien</TableHead>
            <TableHead>Entidad</TableHead>
            <TableHead>Fecha de Ingreso</TableHead>
            <TableHead>Estado</TableHead>
            <TableHead>Ubicación</TableHead>
            <TableHead className="text-right">Acciones</TableHead>
          </TableRow>
        </TableHeader>
        <TableBody>
          {custodias.map((custodia) => (
            <TableRow key={custodia.id}>
              <TableCell className="font-medium">
                {custodia.numeroCertificado}
              </TableCell>
              <TableCell>{custodia.tipoBien}</TableCell>
              <TableCell>{custodia.entidad}</TableCell>
              <TableCell>
                {format(custodia.fechaIngreso, "dd/MM/yyyy", { locale: es })}
              </TableCell>
              <TableCell>
                <Badge className={getEstadoColor(custodia.estado)}>
                  {custodia.estado}
                </Badge>
              </TableCell>
              <TableCell>{custodia.ubicacion}</TableCell>
              <TableCell className="text-right">
                <div className="flex justify-end space-x-2">
                  <Button variant="ghost" size="sm">
                    <Eye className="h-4 w-4 mr-1" />
                    Ver Detalle
                  </Button>
                  <Button variant="ghost" size="sm">
                    <FileText className="h-4 w-4 mr-1" />
                    Emitir CDC
                  </Button>
                </div>
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
      
      {custodias.length === 0 && (
        <div className="text-center py-8">
          <p className="text-gray-500">No se encontraron custodias</p>
        </div>
      )}
    </div>
  );
}
