// Add agencia information
const agencias = [
  { id: "lima", nombre: "Agencia Lima Central" },
  { id: "arequipa", nombre: "Agencia Arequipa" },
  { id: "cusco", nombre: "Agencia Cusco" },
  { id: "trujillo", nombre: "Agencia Trujillo" },
];

export default function SupervisionPage() {
  const [selectedAgencia, setSelectedAgencia] = useState<string>("all");
  const [filteredCustodias, setFilteredCustodias] = useState<Custodia[]>(mockSupervisionData);

  const handleFiltersChange = (filters: any) => {
    let filtered = [...mockSupervisionData];

    if (selectedAgencia && selectedAgencia !== "all") {
      // In real implementation, this would filter by agencia
      filtered = filtered.filter(c => c.id.includes(selectedAgencia === "lima" ? "1" : "2"));
    }

    if (filters.tipoBien) {
      filtered = filtered.filter(c => c.tipoBien === filters.tipoBien);
    }
    if (filters.estado) {
      filtered = filtered.filter(c => c.estado === filters.estado);
    }
    if (filters.fechaDesde) {
      filtered = filtered.filter(c => c.fechaIngreso >= filters.fechaDesde);
    }
    if (filters.fechaHasta) {
      filtered = filtered.filter(c => c.fechaIngreso <= filters.fechaHasta);
    }

    setFilteredCustodias(filtered);
  };

  const getAgenciaStats = (agenciaId: string) => {
    const agenciaData = mockSupervisionData.filter(c => 
      agenciaId === "lima" ? c.id.includes("1") || c.id.includes("2") : c.id.includes("3") || c.id.includes("4")
    );
    return {
      total: agenciaData.length,
      registrados: agenciaData.filter(c => c.estado === "Registrado").length,
      entregados: agenciaData.filter(c => c.estado === "Entregado").length,
      anulados: agenciaData.filter(c => c.estado === "Anulado").length,
    };
  };

  return (
    <div className="space-y-6">
      <div>
        <h1 className="text-2xl font-bold text-gray-900">Supervisión Nacional</h1>
        <p className="text-gray-600">Visualización de custodias por agencias subordinadas</p>
      </div>

      {/* Dashboard Summary */}
      <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
        {agencias.map((agencia) => {
          const stats = getAgenciaStats(agencia.id);
          return (
            <Card key={agencia.id}>
              <CardHeader className="pb-3">
                <CardTitle className="text-lg">{agencia.nombre}</CardTitle>
              </CardHeader>
              <CardContent>
                <div className="space-y-2">
                  <div className="flex justify-between">
                    <span className="text-sm text-gray-600">Total:</span>
                    <span className="font-semibold">{stats.total}</span>
                  </div>
                  <div className="flex justify-between">
                    <span className="text-sm text-gray-600">Registrados:</span>
                    <Badge variant="outline" className="bg-blue-50 text-blue-700">{stats.registrados}</Badge>
                  </div>
                  <div className="flex justify-between">
                    <span className="text-sm text-gray-600">Entregados:</span>
                    <Badge variant="outline" className="bg-green-50 text-green-700">{stats.entregados}</Badge>
                  </div>
                  <div className="flex justify-between">
                    <span className="text-sm text-gray-600">Anulados:</span>
                    <Badge variant="outline" className="bg-red-50 text-red-700">{stats.anulados}</Badge>
                  </div>
                </div>
              </CardContent>
            </Card>
          );
        })}
      </div>

      {/* Filters */}
      <Card>
        <CardHeader>
          <CardTitle>Filtros de Supervisión</CardTitle>
        </CardHeader>
        <CardContent>
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
            <div>
              <Label className="text-sm font-medium text-gray-700 mb-1 block">Agencia</Label>
              <Select value={selectedAgencia} onValueChange={setSelectedAgencia}>
                <SelectTrigger>
                  <SelectValue placeholder="Todas las agencias" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="all">Todas las agencias</SelectItem>
                  {agencias.map((agencia) => (
                    <SelectItem key={agencia.id} value={agencia.id}>
                      {agencia.nombre}
                    </SelectItem>
                  ))}
                </SelectContent>
              </Select>
            </div>
          </div>
        </CardContent>
      </Card>

      {/* Data Table */}
      <div>
        <div className="flex justify-between items-center mb-4">
          <div>
            <h2 className="text-lg font-semibold">Inventario por Agencia</h2>
            <p className="text-sm text-gray-600">
              {selectedAgencia === "all" 
                ? "Mostrando todas las agencias" 
                : `Mostrando: ${agencias.find(a => a.id === selectedAgencia)?.nombre}`
              }
            </p>
          </div>
        </div>
        
        <CustodyTable custodias={filteredCustodias} />
      </div>
    </div>
  );
}
